import {IActionModel, ActionType} from '../models/action.model';
import {MethodType} from '../models/interface.model';

class List {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "backupListAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: List.key,
            type: ActionType.list,
            title: "数据库备份文件管理",
            icon: "backup",
            list: {
                columns: [
                    actionUtils.columnBuilder("<span>{{::item}}</span>", "文件名称").toValue(),
                ],
                showPagination: false,
                searchActionKey: "",
                showRefreshBtn: true,
                showSearchBtn: true,
                showSearchPanel: false,
                toolbars: [],
                itemToolbars: []
            },
            itemActions: [],
            actions: [],
            interfaces: [{
                key: "backupList",
                method: MethodType.GET,
                address: "",
                port: null,
                path: "backups",
                jpp: {
                    set: {"/rows": ""},
                    copy: {},
                    move: {},
                    del: {}
                },
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Backup {
    static key: string = "backupBackupAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Backup.key,
            type: ActionType.confirm,
            title: "备份数据库文件",
            icon: "backup",
            refreshList: true,
            confirm: {
                confirmTitle: "",
                confirmContent: "确定要备份此文件吗!"
            },
            interfaces: [{
                key: "backupDump",
                method: MethodType.POST,
                idFieldPath: "",
                address: "",
                port: null,
                path: "backups",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Delete {
    static key: string = "backupBackupAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Delete.key,
            type: ActionType.confirm,
            title: "删除备份数据库文件",
            icon: "delete",
            refreshList: true,
            confirm: {
                confirmTitle: "",
                confirmContent: "确定要删除备份文件吗!"
            },
            interfaces: [{
                key: "backupDump",
                method: MethodType.POST,
                idFieldPath: "",
                address: "",
                port: null,
                path: "backups",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Dump {
    static key: string = "backupDumpAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Dump.key,
            type: ActionType.confirm,
            title: "还原文件",
            icon: "backup",
            refreshList: true,
            confirm: {
                confirmTitle: "",
                confirmContent: "确定要还原此文件吗!"
            },
            interfaces: [{
                key: "backupDump",
                method: MethodType.POST,
                idFieldPath: "",
                address: "",
                port: null,
                path: "backups",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

export default (module: ng.IModule)=> {
    const services = [List];

    _.each(services, (model)=> {
        module.service(model.key, model);
    });
}



